@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:opsz,wght@9..144,700;9..144,900&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --red: 7 99% 70%;
  --yellow: 51 100% 49%;
  --dark-cyan: 167 40% 24%;
  --dark-blue: 198 62% 26%;
  --moderate-cyan: 168 34% 41%;
  --dark-blue: 212 27% 19%;
  --dark-grayish-blue: 213 9% 39%;
  --grayish-blue: 210 4% 67%;
  --bg-cyan: 168 46% 73%;
  --white: 0 0% 100%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Barlow", sans-serif;
}

body {
  background-color: hsl(var(--white));
  font-size: 1.125rem;
}

p:not([class]) {
  opacity: 0.7;
  max-width: 32ch;
}

/**
 * Colors
 */
.bg-white {
  background: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-moderate-cyan {
  background: hsl(var(--moderate-cyan));
}

.bg-cyan {
  background: hsl(var(--bg-cyan));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 46.25em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-700 {
  margin-block-end: 3rem;
}

/**
 * Padding top and bottom
 */
.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1.25em 2.5em;
  text-decoration: none;
  border-radius: 100vmax;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: var(--fw-bold);
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.header {
  background-image: url("../../../images/mobile/image-header.jpg");
  min-height: 80vh;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 46.25em) {
  .header {
    background-image: url("../../../images/desktop/image-header.jpg");
  }
}

.header-nav {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
}

.header-figure {
  width: 9.375rem;
  height: fit-content;
}
@media (min-width: 46.25em) {
  .header-figure {
    width: 9.375rem;
  }
}

.header-logo {
  width: 100%;
  height: 80%;
}

.header-nav-wrapper {
  display: flex;
  background-color: hsl(var(--white));
  position: absolute;
  right: 30px;
  top: 120px;
  width: calc(100% - 3rem);
  flex-direction: column;
  align-items: center;
  z-index: 1;
}
@media (min-width: 46.25em) {
  .header-nav-wrapper {
    background-color: rgba(255, 0, 0, 0);
    position: relative;
    right: 0;
    top: 0;
    width: auto;
    flex-direction: row;
  }
}
.header-nav-wrapper a.header-menu-item {
  display: none;
  text-decoration: none;
}
@media (min-width: 46.25em) {
  .header-nav-wrapper a.header-menu-item {
    display: flex;
    min-width: 6.25rem;
    justify-content: center;
  }
}

.header-menu-item {
  color: hsl(var(--grayish-blue));
  font-family: "Barlow", sans-serif;
}
@media (min-width: 46.25em) {
  .header-menu-item {
    color: hsl(var(--white));
  }
}

.responsive a:not(:last-child) {
  margin-top: 2.5rem;
}
@media (min-width: 46.25em) {
  .responsive a:not(:last-child) {
    margin-bottom: 0;
  }
}

.responsive a:nth-last-child(1) {
  margin-block: 2.5rem;
}
@media (min-width: 46.25em) {
  .responsive a:nth-last-child(1) {
    margin-bottom: 0;
  }
}

.responsive a:nth-child(n+1) {
  display: flex;
  justify-content: center;
}
.responsive a:nth-child(n+1)::before {
  content: "";
  position: absolute;
  top: -1.5rem;
  right: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0 1.5rem 1.5rem;
  border-color: transparent transparent hsl(var(--white)) transparent;
}

.header-menu-icon {
  display: flex;
}
@media (min-width: 46.25em) {
  .header-menu-icon {
    display: none;
  }
}

.header-menu-btn {
  display: none;
  text-decoration: none;
  background-color: hsl(var(--yellow));
  font-family: "Fraunces", serif;
  color: black;
  text-transform: uppercase;
}
@media (min-width: 46.25em) {
  .header-menu-btn {
    background-color: hsl(var(--white));
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 2.5rem;
  }
  .header-menu-btn:hover, .header-menu-btn:focus {
    color: hsl(var(--white));
    background-color: hsl(var(--white)/0.3);
  }
}

.header-menu-icon {
  text-decoration: none;
  display: block;
  font-size: 1.625rem;
  color: hsl(var(--white));
}
@media (min-width: 46.25em) {
  .header-menu-icon {
    display: none;
  }
}

.header-hero {
  display: flex;
  justify-content: center;
}

.header-hero-title {
  padding-top: 3.125rem;
  font-family: "Fraunces", serif;
  font-size: 2.5rem;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: hsl(var(--white));
  font-weight: 900;
  position: relative;
  text-align: center;
}
.header-hero-title::after {
  content: url(/images/icon-arrow-down.svg);
  position: absolute;
  bottom: -100%;
  right: 47%;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
@media (min-width: 29.375em) {
  .header-hero-title {
    padding-top: 7.5rem;
    font-size: 3rem;
  }
}
@media (min-width: 46.25em) {
  .header-hero-title {
    padding-top: 9.375rem;
    font-size: 3.375rem;
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.futures-card {
  align-items: center;
  text-align: center;
  height: 100%;
}
@media (min-width: 46.25em) {
  .futures-card {
    text-align: left;
    height: 100%;
  }
}

.card-content {
  padding-block: 2.5rem;
  padding-inline: 1.25rem;
}
@media (min-width: 46.25em) {
  .card-content {
    padding-inline: 1.875rem;
    padding-block: 0;
  }
}
@media (min-width: 62.5em) {
  .card-content {
    padding-inline: 6.25rem;
    padding-block: 0;
  }
}

.card-title {
  font-family: "Fraunces", serif;
  font-size: 2.75rem;
  color: hsl(var(--dark-blue));
  font-weight: 900;
}
@media (min-width: 46.25em) {
  .card-title {
    font-size: 1.75rem;
  }
}
@media (min-width: 80em) {
  .card-title {
    font-size: 2.625rem;
  }
}

.card-description {
  color: hsl(var(--dark-grayish-blue));
  font-size: 1.25rem;
  font-weight: 600;
}

.card-picture {
  min-height: 100%;
  min-width: 100%;
}

@media (min-width: 46.25em) {
  .card-dense {
    order: 1;
  }
}

a.card-link {
  display: block;
  position: relative;
  font-size: 1.125rem;
  text-decoration: none;
  font-family: "Fraunces", serif;
  color: hsl(var(--dark-blue));
  font-weight: 900;
  text-transform: uppercase;
}
a.card-link::before {
  position: absolute;
  content: "";
  top: 60%;
  border-radius: 100vmax;
  z-index: -1;
  width: 8.4375rem;
  height: 0.5rem;
  background-color: hsl(var(--yellow)/0.4);
}
a.card-link:hover::before, a.card-link:focus::before {
  background-color: hsl(var(--yellow));
}
@media (min-width: 46.25em) {
  a.card-link {
    text-align: left;
  }
}

a.card-link[data-type=inverted]::before {
  background-color: hsl(var(--red)/0.4);
}
a.card-link[data-type=inverted]:hover::before, a.card-link[data-type=inverted]:focus::before {
  background-color: hsl(var(--red));
}

.card-img-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 60%;
  padding-inline: 3.125rem;
}
@media (min-width: 46.25em) {
  .card-img-text {
    padding-inline: 3.75rem;
    top: 55%;
  }
}
@media (min-width: 62.5em) {
  .card-img-text {
    padding-inline: 6.25rem;
    top: 55%;
  }
}

.card-img-title {
  font-family: "Fraunces", serif;
  font-size: 2.125rem;
  color: hsl(var(--dark-blue)/0.7);
  font-weight: 900;
}
@media (min-width: 46.25em) {
  .card-img-title {
    font-size: 2.375rem;
  }
}

.card-img-description {
  color: hsl(var(--dark-blue)/0.7);
  font-size: 1.125rem;
}
@media (min-width: 46.25em) {
  .card-img-description {
    font-size: 1.125rem;
  }
}

/**
 * TESTIMONIALS 
 */
.testimonials-title {
  font-family: "Fraunces", serif;
  font-size: 1.5rem;
  color: hsl(var(--dark-grayish-blue)/0.7);
  letter-spacing: 6px;
  font-weight: 900;
  text-transform: uppercase;
}
@media (min-width: 46.25em) {
  .testimonials-title {
    font-size: 1.25rem;
  }
}

:where(.flow-testimonials :not(:last-child)) {
  margin-top: var(--flow-spacer, 3em);
}

.testimonials-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-inline: 1.25rem;
}

.testimonials-figure {
  height: 80px;
  width: 80px;
}

.testimonials-img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.testimonials-blockqoute {
  display: block;
  height: 100%;
  color: hsl(var(--dark-grayish-blue));
  line-height: 2;
}

.testimonials-name {
  font-family: "Fraunces", serif;
  font-size: 1.5rem;
  color: hsl(var(--dark-blue));
  font-weight: 900;
}

.testimonials-status {
  font-family: "Barlow", sans-serif;
  font-size: 1rem;
  color: hsl(var(--dark-grayish-blue)/0.5);
  margin-bottom: 3.125rem;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
}

.footer-links,
.footer-logos {
  display: flex;
  justify-content: space-between;
  gap: 1.875rem;
  color: hsl(var(--moderate-cyan));
}
.footer-links:hover, .footer-links:focus,
.footer-logos:hover,
.footer-logos:focus {
  color: hsl(var(--white));
}

a.footer-link,
a.footer-logo {
  text-decoration: none;
  color: hsl(var(--moderate-cyan));
}
a.footer-link:hover, a.footer-link:focus,
a.footer-logo:hover,
a.footer-logo:focus {
  color: hsl(var(--white));
}